.home{

display: flex;
justify-content: center;
align-items: center;
justify-content: space-evenly;
height: 720px;
background-color: rgb(78, 215, 51);

}

.homeimage{

border: 1px solid red;
width: 20%;
border-radius: 50%;
height: 300px;

background-color: black;
}


.homediv{

display: flex;
justify-content: center;
align-items: center;
padding-right: 120px ;



}


.button{


background-color:#047bff;
color: white;
width: 120px;
height: 50px;
border-radius: 10px;
text-align: center;
font-weight: bold;
font-size: 15px;
display: flex;
justify-content: center;
align-items: center;
text-decoration: none;

}






@media (min-width: 481px) and (max-width: 768px) {
    .home {
      display: flex;
      display: grid;
      grid-template-columns: repeat(2,1fr);

     
    }
    
  }

  @media (min-width: 0px ) and (max-width: 480px) {
    .home {
        width: 80%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(1,1fr);
      margin: auto;
      

     
    }
    
  }